import React from 'react'
import socketIOClient from 'socket.io-client'

import config from '../lib/config'
const apiHost = config.apiHost

class TableHost extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			hosts : []
		}
		this.socket = null
	}

	componentWillMount(){
		this.socket = socketIOClient(apiHost)
		this.socket.emit('listHostClient',1)
		this.socket.on('listHostSv',(hosts) => {
			this.setState({
				hosts : hosts
			})
		})
	}
	
	render(){
		var tableElement = this.state.hosts.map((host,i) =>{
						return (<tr key={i}>
									<td>{host.name}</td>
									<td>{host.status === "working" ? <div className="text-success">{host.status}</div> : <div className="text-error">{host.status}</div>}</td>
									<td>{host.timeUpdate}</td>
								</tr>)
					})
		return(
				<table className="table table-striped table-hover">
						<thead>
							<tr>
								<th>Name</th>
								<th>Status</th>
								<th>Time update</th>
							</tr>
						</thead>
					<tbody>
							{tableElement}		    
					</tbody>
				</table>
			)
	}
}
export default TableHost


