import React, { Component } from 'react';
import Notifications from 'react-notify-toast';

class App extends Component {
  render() {
  	const styles = {
  		mt20 : {
  			marginTop : '20px'
  		},
      container : {
        maxWidth: '900px'
      }
  	}
    return (
    	<div className="container" style={styles.container}>
      <Notifications />
	    	<div>
            <ul className="tab tab-block">
              <li className="tab-item"><a href="https://facebook.com/thaotrauz">Facebook</a></li>
            </ul>
	        </div>
	        <div style={styles.mt20}>
	        	{this.props.children}
	        </div>
        </div>
    );
  }
}

export default App;



