import React from 'react'
import {notify} from 'react-notify-toast';
import { Player } from 'video-react';
import socketIOClient from 'socket.io-client'
import TableHost from './TableHost'
import "../../node_modules/video-react/dist/video-react.css"; // import css
import App from '../App'
import config from '../lib/config'
const apiHost = config.apiHost
//const apiHost = "https://sv-data.herokuapp.com"

class Home extends React.Component {
	constructor(props){
		super(props)
		this.state = {
			textLink : "",
			isLoad : false,
			isNotify : false,
			elementPlayer : "",
			sources : [],
			sourcesStream : [],
			file : ""
		}
		this.socket = null
	}
	componentWillMount(){
		this.socket = socketIOClient(apiHost)
		this.socket.on('error',(error) =>{
			notify.show(error,"error",1500)
		})
		this.socket.on('getLinkSv',(data) => {
				if(data.error) {
					notify.show(data.error,"error",1500)
					this.setState({
						isLoad : false
					})
				}else{
					notify.show(data.success,'success')
					var file = data.sourcesStream.length ? data.sourcesStream[0].url : data.sources[0].url
		
						this.setState({
							sources : data.sources,
							sourcesStream : data.sourcesStream,
							elementPlayer : <div>
							<div className="col-6 col-mx-auto">
								<div style={{ marginTop : '20px'}}></div>
								<Player
								      playsInline
								      poster={data.image}
								      src={file}
								    />
							</div>
							  <code className="hljs">{JSON.stringify(data)}</code>
							</div>,
							isLoad  : false
						})
				}
	})
	}
	ValidURL(str) {
	  var regex = /(http|https):[/][/](\w+:{0,1}\w*)?(\S+)(:[0-9]+)?([/]|[/]([\w#!:.?+=&%!\-[/]]))?/;
	  	if(!regex.test(str)) {
	    	return false;
	 	 }else{
	    return true;
	   }
	}
	elementPlayer(){
		return this.state.elementPlayer
	}
	handleChangeTextLink = (e) => {
		this.setState({
			textLink : e.target.value
		})
	} 
	
	handleGetLink = () => {
		if(this.state.isLoad) return 0
		var isUrl = this.ValidURL(this.state.textLink)
		if(!isUrl) return notify.show('Please enter valid url',"error",1500)
		this.setState({
			isLoad : true,
		})
		const { textLink } = this.state
		this.socket.emit('getLinkClient',textLink)
	}

	render(){
		return(
			<App>
			<div className="columns">
				<div className="column col-12">
					<div className="input-group">
		                  <input type="url" className="form-input" placeholder="Link" onChange={this.handleChangeTextLink} />
		                  <button className={this.state.isLoad ? "btn btn-primary input-group-btn loading" : "btn btn-primary input-group-btn"} onClick={this.handleGetLink}>Get link</button>
		            </div>
				</div>
				<div className="column col-12" style={{marginTop : '20px'}}>
					<div className="panel">
						  <div className="panel-header">
						    <div className="panel-title"><span className="text-primary">Host working</span></div>
						  </div>
						  <div className="panel-body">
						  <p>Vẩn còn rất nhiều host bạn cứ nhập vào và get !!!</p>
							<div className="tile">
							    <TableHost />
							</div>
						  </div>
					</div>
				</div>
				<div className="column col-12">
					{this.elementPlayer()}
				</div>
			</div>
			</App>
			)
	}
}

export default Home


